body {
	min-width: 320px;
	color: $black;
	background: $white;
	font-family: $fontPrimary;
	@include f(12px, 400, 1.3);
	@include md {
		padding-top: 70px; } }

.container {
	position: relative;
	margin: 0 auto;
	width: 100%;
	max-width: 1440px; }

.inner {
	position: relative;
	z-index: 100;
	padding: 0 20%;
	@include clr;
	@include md {
		padding-left: 30px;
		padding-right: 30px; } }

.fullscreen {
	position: relative;
	height: 100vh;
	&__section {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		display: none;
		@include md {
			position: relative;
			display: block;
			opacity: 0;
			// visibility: hidden
 }			// transition: opacity 0.7s, visibility 0.7s
		&.is-useless {
			pointer-events: none; }
		&.is-active {
			z-index: 10;
			// +md
			// 	opacity: 1
 } }			// 	visibility: visible
	&__nav {
		position: absolute;
		z-index: 300;
		top: 50%;
		left: 45px;
		@include md {
			display: none; }
		&[data-theme="white"] {
			.fullscreen__nav-btn {
				&:before {
					background: $white; } } }
		&-btn {
			padding: 11px 0;
			background: none;
			&.is-active {
				&:before {
					transform: scaleX(1); } }
			&:before {
				content: '';
				display: block;
				width: 35px;
				height: 3px;
				background: $blue;
				transform: scaleX(0.5);
				transform-origin: left center;
				transition: transform 0.3s, background 0.3s; } } } }
